import React from 'react'
import { useHistory } from 'react-router-dom';
import { Link,useLocation } from "react-router-dom";

const Navbar = () => {
    let history=useHistory();
      let location=useLocation();
      // useEffect(()=>{
      //   console.log(location.pathname); 
      // },[location])

const handleLogout=()=>{
  localStorage.removeItem('sah')
  history.push("/login")
}

    return (
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
        <div className="container-fluid">
          <Link className="navbar-brand" to="/">NotesHub</Link>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link className={`nav-link ${location.pathname==="/"?"active":{}}`} aria-current="page" to="/">Home</Link>
              </li>
              <li className="nav-item">
                <Link className={`nav-link ${location.pathname==="/about"?"active":{}}`}to="/about">About</Link>
              </li>
            
             
            </ul>
            {!localStorage.getItem('sah')?<form className="d-flex">
              <Link className="btn btn-primary mx-2" to="/login" role="submit">Login</Link>
              <Link className="btn btn-primary mx-2" to="/signup" role="submit">Signup</Link>
            </form>:<button onClick={handleLogout} className="btn btn-primary">Logout</button>}
          </div>
        </div>
      </nav>
    )
}

export default Navbar
